import React from "react";
import PropTypes from "prop-types";
import { makeStyles } from "@material-ui/core/styles";
import { Chip, Typography, Grid } from "@material-ui/core/";
import moment from "moment-timezone";

const useStyles = makeStyles(theme => ({
  chip: {
    marginRight: "10px",
    marginBottom: "5px"
  },
  bottom: {
    color: "#9e9e9e",
    fontSize: "12px",
    marginRight: "16px"
  },
  date: {
    color: "#9e9e9e",
    fontSize: "10px",
    marginRight: "16px"
  }
}));

function MeetBoard({ board }) {
  const classes = useStyles();
  let personString = "1:1";
  switch (board.personTag) {
    case 1:
      personString = "1:1";
      break;
    case 2:
      personString = "2:2";
      break;
    case 3:
      personString = "3:3";
      break;
    case 4:
      personString = "4:4";
      break;
    case 5:
      personString = "다수";
      break;
    default:
      break;
  }

  let sexColor = "primary";
  let commentColor = "secondary";
  if (board.uid.sex === 1) {
    sexColor = "secondary";
    commentColor = "primary";
  }
  return (
    <div
      style={{ paddingTop: "20px", paddingLeft: "20px", paddingRight: "20px" }}
    >
      <div style={{ marginBottom: "10px" }}>
        <Chip
          className={classes.chip}
          size="small"
          label={personString}
          color={sexColor}
        />
        <Chip
          className={classes.chip}
          size="small"
          label={board.ageTag}
          color={sexColor}
        />
        <Chip
          className={classes.chip}
          size="small"
          label={board.regionTag}
          color={sexColor}
        />
      </div>

      <Typography className={classes.title} noWrap variant="h6" gutterBottom>
        {board.title}
      </Typography>
      {board.content.split("\n").map((i, key) => {
        return (
          <Typography key={key} className={classes.content} variant="body2">
            {i}
          </Typography>
        );
      })}
      <Grid
        container
        direction="row"
        justify="flex-start"
        alignItems="center"
        style={{ paddingTop: "20px" }}
      >
        <Typography className={classes.bottom}>{board.uid.company}</Typography>

        <Typography className={classes.bottom}>{board.uid.nickname}</Typography>
      </Grid>
      <Typography className={classes.date}>
        {moment.tz(board.create_date, "Asia/Seoul").format("YY.MM.DD HH:mm")}
      </Typography>
      <Chip
        size="small"
        label={"댓글  " + board.comments.length}
        color={commentColor}
        style={{ marginTop: "15px" }}
      />
    </div>
  );
}
export default MeetBoard;

MeetBoard.propTypes = {
  post: PropTypes.object
};
